.grid {
    width: 85vw;
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
    grid-gap:20px;
    margin: 0 auto;
}

.modal {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.modal p {
    color: red;
    text-decoration: underline;
    cursor: pointer;
}

.modal button {
    display: block;
    margin: 10px;
    padding: 10px 30px;
}