.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    transition: 0.2s;
    background: rgba(183, 207, 202, 0.746);
    border-radius: 10px;
    text-align: center;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.05);
}